import React, { useMemo } from 'react'
import { useSelector } from 'react-redux'
import { CustomTable } from '../../reuseableComponents'
import { getBadgeColor } from '../../../utils/common'
import moment from 'moment'
import { CustomButton } from '../../globalComponents'

const LeaveSettingTable = (props) => {
  const {
    allLeaveSettingData,
    setSelectedLeaveData,
    handleAddUpdateLeaveModal,
    toggleDeleteSettingTypeModal,
    currentPage,
    setCurrentPage,
    handleOpenClose
  } = props

  const { getAllLeaveSettingPending } = useSelector(state => state.leave)
  const fields = [
    { key: 'sn', label: 'S.N' },
    { key: 'leave_type_name', label: 'Leave Name' },
    { key: 'department_name', label: 'Department Name' },
    { key: 'designation_name', label: 'Designation Name' },
    { key: 'base_on_credit_name', label: 'Base On Credit Name' },
    { key: 'is_allowed_on_notice', label: 'Allowed On Notice' },
    { key: 'is_active', label: 'Status' },
    // { key: 'created_at', label: 'Created At' },
    { key: 'action', label: 'Action' }
  ];

  const scopedSlots = useMemo(() => ({
    sn: (_, index) => (
      <span>{index + 1}</span>
    ),
    is_active: item => (
      <span
        className={`badge ${getBadgeColor(item.is_active ? "Active" : "Inactive")}`}
      >
        {item.is_active ? "Active" : "Inactive"}
      </span>
    ),
    is_allowed_on_notice: item => (
      <span
        className={`badge ${getBadgeColor(item.is_allowed_on_notice ? "Active" : "Inactive")}`}
      >
        {item.is_allowed_on_notice ? "Yes" : "No"}
      </span>
    ),

    created_at: item => moment(item.created_at).format("DD-MM-YYYY"),
    action: item => (
      <div className="actionGroup d-flex gap-2">
        <CustomButton
          text="Update"
          classNames="btn btn-primary"
          handleClick={() => {
            handleOpenClose(item);
          }}
        />
        <CustomButton
          text="Delete"
          handleClick={() => toggleDeleteSettingTypeModal(item._id)}
          classNames="btn btn-danger"
        />
      </div>
    )
  }), [allLeaveSettingData]);

  return (
    <>
      <CustomTable
        data={allLeaveSettingData?.data}
        fields={fields}
        isLoading={getAllLeaveSettingPending}
        pagination={true}
        currentPage={currentPage}
        setCurrentPage={setCurrentPage}
        totalPages={allLeaveSettingData?.totalPages}
        tableName={"Leave Setting"}
        // onSearch={handleSearch}
        scopedSlots={scopedSlots}
      />
    </>
  )
}

export default LeaveSettingTable
